
.editUsers {
    // border: 1px solid black;
    padding-top: 40px;
}
.editUsers__h2 {
    font-size: 27px;
    font-weight: 500;
    letter-spacing: 10px;
    text-align: center;
}

.editUsers__container {
    margin: 0 auto;
    margin-top: 100px;
    max-width: 86%;
}
.editUsers__container_2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.editUsers__select {
    width: 500px;
    height: 48px;
}
.editUsers__select.length {
    border: 1px solid red !important;
}
.editUsers__information {
    margin-top: 80px;
    font-size: 27px;
    font-weight: 500;
}

.editUsers__length {
    color: red;
}

.editUsers__input {
    width: 500px;
    height: 48px;
}

.editUsers__column {
    flex-basis: 45%;
    margin-top: 70px;
}