
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.408);

    .modal__block {
        position: relative;
        background-color: white;
        max-width: 700px;
        margin: 0 auto;
        top: 20%;
    }

    .modal__title {
        letter-spacing: 10px;
        text-align: center;
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 500;
        padding-top: 30px;
    }
    .modal__content {
        padding: 60px;
        padding-bottom: 100px;
        margin-top: 20px;
    }
    .modal__form {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .modal__items {
        position: relative;
        margin-top: 50px;
    }
    .modal__input {
        width: 250px;
        height: 40px;
        padding-left: 10px;

        &::placeholder {
            font-size: 15px;
        }
    }
    .modal__select {
        width: 265px;
        height: 47px;
        padding-left: 10px;
    }
    .modal__select__2 {
        // margin-top: 20px;
        width: 265px;
        height: 47px;
        padding-left: 10px;
    }
    .modal__label {
        position: absolute;
        top: -26px;
    }

    .modal__buttons {
        display: flex;
        margin-top: 60px;
        justify-content: flex-end;
    }

    .modal__btn {
        border: 1px solid black;
        width: 120px;
        text-align: center;
        padding-top: 10px;
        height: 40px;
        cursor: pointer;
        transition: all 400ms ease;

        &:hover {
            background-color: black;
            color: white;
        }

        &:last-of-type {
            margin-left: 10px;
        }
    }
    .modal__error {
        color: red;
    }
}