
.users {
    padding: 100px;
    .users__title {
        margin-top: 40px;
        text-align: center;
        font-size: 27px;
        font-weight: 500;
        letter-spacing: 10px;
    }

    .users__filters {
        display: flex;
        margin-top: 30px;
        // border: 1px solid red;
    }
    .users__filter {
        flex-basis: 20%;
        border: 2px solid black;
        border-radius: 5px;
        margin-right: 30px;
    }
    .users__filter__2 {
        flex-basis: 20%;
        border: 2px solid black;
        border-radius: 5px;
        // max-height: 50px;
        height: 100%;
    }
    .users__filter__2.active {
        max-height: 400px !important;
        height: 100%;
    }
    .users__filter__2.disabled {
        pointer-events: none;
        opacity: 0.5;
        cursor: not-allowed;
        user-select: none;
    }
    .users__filter__3 {
        flex-basis: 20%;
        border: 2px solid black;
        border-radius: 5px;
        height: 100%;
        margin-left: 30px;
    }
    .users__department {
        display: flex;
        align-items: center;
        padding: 15px;
    }
    .users__prof {
        margin-left: 5px;
        color: rgb(96, 95, 95);
    }
    .users__show {
        cursor: pointer;
    }

    .users__content {
        overflow-y: auto;
        max-height: 120px;
        // scrollbar-color: #0f0e0e #e0e0e0;
        // scrollbar-width: thin;
        direction: ltr;

        &::-webkit-scrollbar {
            cursor:grab !important;
            width: 17px;
            background-color: transparent;
        }
         
         &::-webkit-scrollbar {
           cursor:grab !important;
        }
        &::-webkit-scrollbar-thumb {
            min-height: 60px;
            cursor:grab !important;
            background-color: rgb(0, 0, 0);
            border-radius: 10px;
          border: 6px solid rgba(0, 0, 0, 0);
          background-clip: padding-box;
        }

    }
    .users__block {
        display: flex;
        padding: 15px;
        justify-content: space-between;
    }

    .users__checkbox {
        appearance: none;
        width: 30px;
        -webkit-appearance: none;
        -moz-appearance: none;
        height: 30px;
        border: 1px solid black;
        cursor: pointer;
        position: relative;

        &:checked {
            background-color: black;
        }

        &:checked::before {
            content: '';
            position: absolute;
            top: 10%;
            left: 30%;
            width: 9px;
            height: 14px;
            border: solid #fff;
            border-width: 0 3px 3px 0;
            transform: rotate(45deg);
        }
    }
    .users__icon {
        font-weight: 700;
        margin-right: 10px;
    }
    .user__trash {
        margin-left: 40px;
        border: 1px solid black;
        height: 100%;
        padding: 13px;
        cursor: pointer;
        font-size: 19px;
        text-align: center;
        border-radius: 5px;
        transition: all 400ms ease;

        &:hover {
            background-color: black;
            color: white;
        }
    }

    .user__addUser {
        border: 1px solid black;
        height: 100%;
        width: 150px;
        cursor: pointer;
        text-align: center;
        padding: 18px;
        margin-left: 250px;
        transition: all 400ms ease;
        border-radius: 5px;

        &:hover {
            background-color: black;
            color: white;
        }
    }
}