

.table {
    .table__names {
        border: 1px solid rgba(128, 128, 128, 0.63);
        // max-width: 80%;
        // margin: 0 auto;
        // padding: 25px;
        padding-top: 25px;
        padding-bottom: 25px;
        font-weight: 700;
        // padding-left: 150px;
        // padding-right: 150px;
        display: flex;
        justify-content: space-around;
    }
    .table__params {
        flex-basis: 25%;
        text-align: center;
    }

    .table__users {
        display: flex;
        justify-content: space-between;
